import "./styles.css";
import "antd/dist/antd.css";
import {
  Statistic,
  Row,
  Col,
  Select,
  InputNumber,
  Form,
  Button,
  Radio
} from "antd";
import { useState } from "react";

const { Option } = Select;

export default function App() {
  const [form] = Form.useForm({ unit: 2 });
  const [showManualY, setShowManualY] = useState(false);
  const [res, setRes] = useState({});

  const onFinish = ({ weight, Y, typeInY }) => {
    const rer = Math.sqrt(Math.sqrt(Math.pow(weight, 3))) * 70;
    console.log(weight, Y, typeInY);
    setRes({
      rer,
      everydatCal: rer * (Y === "lose" || Y === "grow" ? typeInY : Y)
    });
  };

  return (
    <div className="App">
      <h1>Calorie Calculator</h1>
      <Form form={form} name="control-hooks" onFinish={onFinish}>
        <Form.Item name="unit" label="Unit">
          <Radio.Group>
            <Radio value={1}>A</Radio>
            <Radio value={2}>B</Radio>
            <Radio value={3}>C</Radio>
            <Radio value={4}>D</Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item
          name="weight"
          label="Weight(kg)"
          rules={[{ required: true, message: `Please input the weight!` }]}
        >
          <InputNumber min={1} max={15} />
        </Form.Item>
        <Form.Item
          label="Y"
          name="Y"
          rules={[{ required: true, message: `Please input the Y!` }]}
        >
          <Select
            placeholder="Select a option and change input text above"
            allowClear
            onSelect={(e) => setShowManualY(e === "lose" || e === "grow")}
          >
            <Option value="1.8">成年-1.8</Option>
            <Option value="1.6">绝育-1.6</Option>
            <Option value="1.4">老年-1.4</Option>
            <Option value="1.2">易胖-1.2</Option>
            <Option value="lose">减重-(0.8-1.0)</Option>
            <Option value="grow">生长发育期-(2-3)</Option>
          </Select>
        </Form.Item>
        {showManualY && (
          <Form.Item
            name="typeInY"
            label="Type-In Y"
            rules={[{ required: showManualY }]}
          >
            <InputNumber min={0} max={25} precision={1} step={0.1} />
          </Form.Item>
        )}
        <Form.Item>
          <Button type="primary" htmlType="submit">
            Calculate
          </Button>
        </Form.Item>
      </Form>
      {Object.keys(res).length ? (
        <Row gutter={16}>
          <Col span={12}>
            <Statistic title="RER" value={res?.rer?.toFixed(2)} />
          </Col>
          <Col span={12}>
            <Statistic
              title="每日摄入量"
              value={res?.everydatCal?.toFixed(2)}
            />
          </Col>
        </Row>
      ) : null}
    </div>
  );
}
